.lineC {
  display: flex;
  margin-left: -60px;
  margin-top: 100px;
  position: relative;

  margin-right: -8px;
}

.line {
  height: 3px;
  background-color: #10c237;
  width: 110px;
  position: relative;
  z-index: -1;
}

.circkle {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
  background-color: #10c237;
  border-radius: 50%;
  position: relative;
  top: -8px;
  z-index: 1;
}

@media screen and (max-width: 768px) {
  .lineC {
    display: none;
  }
}
