.wallet-adapter-button-trigger{
  padding: 0;
    border: 0;
    cursor: pointer;
    text-transform: uppercase;
    border-radius: 8px;
    background: #10C237;
    font-weight: 500;
    color: #fff;
    padding: 0 20px;
    font-size: 16px;
    line-height: 50px;
    white-space: nowrap;
}
.loading{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  background: rgba(0, 0, 0, 0.3);
}
.loading img{
  width: 100px;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -50px 0 0 -50px;
}
.modal{
  width: 300px;
}
.modal input{
  width: 100%;
  padding: 20px 10px;
  font-size: 16px;
  background: rgba(255, 255, 255, .1);
  font-size: 20px;
  color: #fff;
  border: 0;
}
.modal .buttons{
  padding: 20px 0 0 0;
}
.modal button{
  width: 100%;
  box-sizing: border-box;
  color: #fff;
  font-size: 16px;
  line-height: 46px;
  border-radius: 4px;
  border: 0;
  cursor: pointer;
  text-transform: uppercase;
  width: 48.5%;
  margin-bottom: 10px;
}
.modal h5{
  padding-bottom: 10px;
  font-size: 23px;
}
.ReactModal__Content{
  background: #14170E!important;
  border: 2px solid #10C237!important;
  padding: 20px!important;
  border-radius: 8px!important;
  padding: 30px 20px 10px 20px!important;
}
.ReactModal__Overlay{
  background: rgba(0, 0, 0, 0.3)!important;
}
.tr-ok{
  background: #10C237;
  margin-right: 1.25%;
}
.tr-cancel{
  background: rgba(255, 255, 255, .1)!important;
  margin-left: 1.25%;
}
.full{
  width: 100%!important;
}
.logo{
  width: 20px;
  margin: 0 0 0 5px;
  position: relative;
  top: 5px;
}
.studio{
  margin: 0 0 0 10px;
}