.proofWork {
  display: flex;
  flex-direction: column;
  margin-top: 75px;
}
.textBlock {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  line-height: 105%;
}
.description {
  color: rgba(255, 255, 255, 0.8);
  font-family: "Blender Pro";
  font-size: 18px;
  font-style: normal;
  font-weight: 300;
  max-width: 50%;
  line-height: 25px;
}
.title {
  color: #fff;
  font-family: "Blender Pro";
  font-size: 90px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
}

.proofScheme {
  margin-top: 60px;
  position: relative;
}

.gpuBtn {
  display: inline-flex;
  padding: 19px 29px;
  align-items: center;
  gap: 20px;
  border-radius: 8px;
  background: #10c237;
  border: none;
  cursor: pointer;
  box-shadow: 0px 0px 12.3px 0px rgba(11, 255, 64, 0.41);
  max-height: 60px;
}
.gpuBtn span {
  color: #fff;
  font-family: "Blender Pro";
  font-size: 24px;
  font-style: normal;
  font-weight: 900;
  line-height: 120%; /* 28.8px */
  text-transform: uppercase;
}

.gpuBtn img {
  width: 24px;
  height: 24px;
}

.buttonWithLine {
  display: flex;
}

.line {
  margin-top: 20px;
}

.lines {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 60px;
  left: 110px;
}

@media screen and (max-width: 1300px) {
  .title {
    font-size: 60px;
  }
}

@media screen and (max-width: 868px) {
  .textBlock {
    display: flex;
    flex-direction: column;
  }

  .title {
    margin-bottom: 30px;
  }
  .description {
    max-width: 100%;
  }
}

@media screen and (max-width: 600px) {
  .title {
    font-size: 50px;
  }
}
