.square {
  margin-top: 52px;
  max-width: 809px;
  min-height: 784px;
  margin-left: 160px;
  padding: 20px;

  margin-bottom: 50px;
  border-radius: 8px;
  border: 3px solid rgba(16, 194, 55, 0.24);
  background: rgba(20, 23, 14, 0.1);
  backdrop-filter: blur(20px);
}

.gpuItem {
  display: flex;
  max-height: 120px;
  align-items: center;
  margin-bottom: 60px;
  padding-top: 0px;
}

.personal {
  max-width: 240px;
  max-height: 117px;

  margin-top: 80px;
}

.gpu {
  max-width: 400px;
  max-height: 117px;
  margin-bottom: 55px;
}
.gpu img {
  width: 100%;
  height: 100%;
}

.personal img {
  width: 100%;
  height: 100%;
}

.dottedLine {
  display: flex;
  margin-left: -46px;
  margin-top: 80px;
  position: relative;
  z-index: -1;
  margin-right: -3px;
}

.dot {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
  background-color: #10c237;
  border-radius: 50%;
  top: 116px;
  left: 10px;
  z-index: 1;
  position: absolute;
}

.dot1 {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
  background-color: #10c237;
  border-radius: 50%;
  top: 297px;
  left: 10px;

  z-index: 1;
  position: absolute;
}
.dot2 {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
  background-color: #10c237;
  border-radius: 50%;
  top: 477px;
  left: 10px;
  z-index: 1;
  position: absolute;
}

@media screen and (max-width: 868px) {
  .square {
    margin-left: 0px;
  }
}

@media screen and (max-width: 768px) {
  .dottedLine {
    display: none;
  }
}

@media screen and (max-width: 1300px) {
  .dot,
  .dot1,
  .dot2 {
    display: none;
  }
}
